import PropTypes from 'prop-types';
// @mui
import { Box, Chip, Link, Stack, Typography } from '@mui/material';
import Iconify from './Iconify';
import { UserChip } from './AuthorChip';

// ----------------------------------------------------------------------

PaperCard.propTypes = {
  paper: PropTypes.object,
};

// ----------------------------------------------------------------------

export function PaperCard({ paper }) {
  return (
    <Box>
      <Stack spacing={1}>
        <Link href={paper.link} color="inherit">
          <Typography variant={'h5'} line={2}>
            {paper.title}
          </Typography>
        </Link>
        <Stack direction="row" flexWrap="wrap">
          {paper.authors.map((author) => (
            <UserChip key={author.displayName} user={author} sx={{ mr: 1, mt: 0.5 }} />
          ))}
        </Stack>
        {paper.media}
        <Typography variant="body1">{paper.content}</Typography>
        <Stack direction="row" flexWrap="wrap" justifyContent="flex-end">
          {paper.badges.map((badge) => (
            <Chip
              key={badge.label}
              icon={<Iconify icon={badge.icon} />}
              label={badge.label}
              color="primary"
              variant="outlined"
              size="small"
              // clickable={badge?.link !== undefined}
              sx={{ mr: 1, mt: 0.5 }}
            />
          ))}
        </Stack>
      </Stack>
    </Box>
  );
}
