import { Box, Stack } from '@mui/material';
import { me, nivgi, oravioz, stav, tomero, yaniv, zachav } from '../authors';

const ocrContent = (
  <Stack direction="column" spacing={0.5}>
    <div>
      <strong>TL;DR: </strong>Efficient Text Detection is a challenging problem due to the high variability in text
      scale and location. In order to successfully capture small text many methods use an enlarged image or even several
      rescaled ones. This significantly increases the processing time and needlessly enlarges background regions. We
      propose to learn a "scale predictor" head and use it to efficiently approximate the text regions and their
      respective scale. We then create a compact image containing only text regions, resized to a canonical scale, which
      is fed again to the segmentation network for fine-grained detection.
    </div>
  </Stack>
);

