import PropTypes from 'prop-types';
// @mui
import { Avatar, Chip, Link } from '@mui/material';

// ----------------------------------------------------------------------

UserChip.propTypes = {
  user: PropTypes.object,
};

// ----------------------------------------------------------------------

export function UserChip({ user, ...other }) {
  // TODO: Create a dedicated object for chart here so that this can be in a different file

  const getInitials = (str) =>
    str
      .split(' ')
      .map((n) => n[0])
      .join('.');
  // seperate users from other authors

  // const user = getUserByName(userName);
  // TODO: This would become async for real data, as I access another DB here
  // NOTE: Though, probably would be smart to get from the server the data for the authors at the same time
  const chipContent = (
    <Chip
      avatar={
        user?.photoURL ? (
          <Avatar src={user.photoURL} />
        ) : (
          <Avatar color="primary">{getInitials(user.displayName)}</Avatar>
        )
      }
      label={user.displayName}
      variant={user?.me ? 'filled' : 'outlined'}
      clickable={user?.link !== undefined}
      {...other}
    />
  );
  return user?.link ? (
    <Link href={user.link} color="inherit" sx={{ textDecoration: 'none' }}>
      {chipContent}
    </Link>
  ) : (
    chipContent
  );
}
