import { Stack } from '@mui/material';
import { matansel, me, ronnykimmel } from '../authors';

const content = (
  <Stack direction="column" spacing={0.5}>
    <div>
      <strong>TL;DR: </strong>The last in our face reconstruction trilogy. Instead of learning the facial
      reconstructions in the 3DMM subspace we use the pix2pix framework to directly map an input image to its depth
      image and facial correspondence map. The pixel-based mapping is then used to create an high quality reconstruction
      using a purely geometric refinement process. In this formulation the 3DMM is still used to generate the synthetic
      data, however the learned network is not directly restricted to that subspace and was able to provide
      reconsturctions that do no reside in the 3DMM space.
    </div>
    {/* <div> */}
    {/*   Our approach offered a powerful alternative to fixed scaling schemes, achieving an equivalent accuracy to larger */}
    {/*   input scales while processing far fewer pixels, especially on images with sparse text instances. */}
    {/* </div> */}
  </Stack>
);

