import PropTypes from 'prop-types';
// material
import { alpha, styled } from '@mui/material/styles';
import { AppBar, Box, Stack, Toolbar, Typography } from '@mui/material';
import useScrollTrigger from '@mui/material/useScrollTrigger';

// components
//
import useResponsive from '../../hooks/useResponsive';

// ----------------------------------------------------------------------

// const DRAWER_WIDTH = 280;
const APPBAR_MOBILE = 64;
const APPBAR_DESKTOP = 72;

const RootStyle = styled(AppBar)(({ theme }) => ({
  boxShadow: 'none',
  backdropFilter: 'blur(6px)',
  WebkitBackdropFilter: 'blur(6px)', // Fix on Mobile
  backgroundColor: alpha('#1939B7', 0.9),
  [theme.breakpoints.up('lg')]: {
    width: `100%`,
  },
}));

const ToolbarStyle = styled(Toolbar)(({ theme }) => ({
  minHeight: APPBAR_MOBILE,
  [theme.breakpoints.up('lg')]: {
    minHeight: APPBAR_DESKTOP,
    padding: theme.spacing(0, 7),
  },
}));

const HoverStyle = styled(Typography)(({ theme }) => ({
  ...theme.typography.subtitle1,
  cursor: 'pointer',
  transition: theme.transitions.create('opacity', {
    duration: theme.transitions.duration.shorter,
  }),
  '&:hover': {
    opacity: 0.48,
    textDecoration: 'none',
  },
}));

// ----------------------------------------------------------------------

DashboardNavbar.propTypes = {
  publicationsRef: PropTypes.object,
  projectsRef: PropTypes.object,
};

export default function DashboardNavbar({ publicationsRef, projectsRef }) {
  // const triggerSomeScroll = useScrollTrigger({ threshold: 0 });
  const triggerEndAbout = useScrollTrigger({ threshold: 300, disableHysteresis: true });
  const triggerEndPublications = useScrollTrigger({ threshold: 3300, disableHysteresis: true });
  const colorAbout = !triggerEndAbout && !triggerEndPublications;
  const colorPublications = triggerEndAbout && !triggerEndPublications;
  const colorProjects = triggerEndPublications;
  const isDesktop = useResponsive('up', 'lg');

  function handlePublicationsClick() {
    publicationsRef.current.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'nearest' });
  }

  function handleProjectsClick() {
    projectsRef.current.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'nearest' });
  }

  return (
    <RootStyle>
      <ToolbarStyle>
        {/* <IconButton onClick={onOpenSidebar} sx={{ mr: 1, color: 'text.primary', display: { lg: 'none' } }}> */}
        {/*   <Iconify icon="eva:menu-2-fill" /> */}
        {/* </IconButton> */}
        <Typography variant="h4">Elad Richardson</Typography>
        {/* <Searchbar /> */}
        <Box sx={{ flexGrow: 1 }} />
        {isDesktop && (
          <Stack direction="row" alignItems="center" spacing={{ xs: 0.5, sm: 2.5 }}>
            <HoverStyle
              onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
              sx={{ opacity: colorAbout ? 0.48 : 1.0 }}
            >
              TL;DR
            </HoverStyle>
            <HoverStyle onClick={() => handlePublicationsClick()} sx={{ opacity: colorPublications ? 0.48 : 1.0 }}>
              Publications
            </HoverStyle>
            <HoverStyle onClick={() => handleProjectsClick()} sx={{ opacity: colorProjects ? 0.48 : 1.0 }}>
              Projects
            </HoverStyle>
          </Stack>
        )}
      </ToolbarStyle>
    </RootStyle>
  );
}
